<template>
  <div class="app-page page-content-bottom-menu">
    <page-content class="page-content">
      <div class="div">
        <div><img class="top-img" src="/static/images/shopping-cart/spxx.png"/></div>
        <yd-cell-group>
          <yd-cell-item class="cell">
            <span class="span-left" slot="left">姓名：</span>
            <yd-input class="span-right" :show-success-icon="false" slot="right" :show-error-icon="false" required  max="20" placeholder="请输入用户名"></yd-input>
          </yd-cell-item>
          <yd-cell-item class="cell">
            <span class="span-left" slot="left">手机号：</span>
            <yd-input class="span-right" slot="right" :show-success-icon="false" regex="mobile" placeholder="请输入手机号码"></yd-input>
          </yd-cell-item>
          <yd-cell-item class="cell">
            <span class="span-left" slot="left">性别：</span>
            <yd-input class="span-right" v-model="sex" :on-focus="a" :on-blur="b" :show-success-icon="false" slot="right" :show-error-icon="false" required placeholder="请选择性别"></yd-input>
          </yd-cell-item>
          <yd-cell-item class="cell">
            <span class="span-left" slot="left">推荐人：</span>
            <yd-input class="span-right" slot="right" :show-success-icon="false" regex="mobile" placeholder="非必填"></yd-input>
          </yd-cell-item>
        </yd-cell-group>
        <van-button class="bottom">提交</van-button>
        <van-popup v-model="show" position="bottom">
          <van-picker
            show-toolbar
            title="性别"
            :columns="columns"
            @cancel="onCancel"
            @confirm="onConfirm"
          />
        </van-popup>
      </div>
    </page-content>
  </div>
</template>

<script>
import PageContent from '@/components/page/content'
export default {
  name: 'personalCnterUpdPersonalData',
  components: {
    PageContent
  },
  data () {
    return {
      sex: '男',
      columns: ['男', '女'],
      show: false
    }
  },
  methods: {
    onCancel () {
      this.show = false
    },
    onConfirm (value, index) {
      this.sex = value
      this.show = false
    },
    a () {
      this.show = true
    },
    b () {
      if (this.sex !== '男' && this.sex !== '女') {
        this.sex = '男'
      }
    }
  },
  created () {
  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .top-img{
    margin-top: 10px;
    margin-bottom: 40px;
    width: 114px;
    height: 114px;
  }
  .page-content{
    text-align: center;
    padding: 60px 50px 0!important;
    background-color: #F2F2F2;
  }
  .div{
    width: 650px;
    padding: 50px 20px 0;
    height: 900px;
    background-color: $whiteffffff;
    border-radius: 8px;
    .cell{
      height: 90px;
      border-bottom: 2px solid $whitef7f7f7;
    }
    .bottom{
      margin-top: 110px;
      line-height: 76px;
      width: 550px;
      height: 76px;
      font-family: $font-family-Bold;
      font-size: 28px;
      font-weight: normal;
      font-stretch: normal;
      color: $whiteffffff;
      background-color: #22ac38;
      border-radius: 8px;
    }
    .span-left{
      height: 26px;
      font-family: ﻿$font-family-Medium;
      font-size: 28px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 26px;
      color: #333333;
    }
    /deep/ .yd-input>input{
      text-align: right;
    }
    .span-right{
      height: 26px;
      font-family: $font-family-Medium;
      font-size: 28px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 26px;
      color: #666666;
    }
  }
</style>



